.alignment {
  // position: relative;
  @include positioning;
}

.hero {
  // position: relative;
  padding-top: 194px;
  overflow: hidden;

  background-color: getColor('primary-color');

  @media screen and (min-width: 385px) and (max-width: 767px) {
    padding-bottom: 158px;
    background-position: 50% 100%;
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-bottom: 158px;
  }

  @include tablet {
    padding-top: 102px;
    padding-bottom: 56px;
  }

  @include desktop {
    padding-top: 147px;
    padding-bottom: 84px;
  }
}

.achive-title--wrap {
  // position: relative;
  display: flex;
  justify-content: space-between;
  @include tablet {
    margin-bottom: 61px;
  }
}

.hero__title {
  max-width: 218px;
  font-family: $fontSecondary;
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: getColor('white-text');

  margin-bottom: 20px;

  @include tablet {
    font-size: 22px;
    line-height: 1.13;
    max-width: 162px;
  }
  @include desktop {
    font-size: 38px;
    line-height: 1.15;
    max-width: 277px;
    margin-bottom: 24px;
  }
}

.hero__title--color {
  font-size: 48px;
  line-height: 96.5%;

  text-transform: uppercase;
  color: getColor('primary-text');

  @include tablet {
    font-size: 34px;
    line-height: 90.5%;
  }
  @include desktop {
    font-size: 58px;
    line-height: 90.5%;
  }
}

.btn {
  @extend %btn-primary;
  font-size: 8px;
  line-height: 1.25;
  padding-top: 7px;
  padding-bottom: 7px;
  padding-right: 14px;
  padding-left: 14px;

  @include desktop {
    font-size: 14px;
    line-height: 1.28;
    padding-top: 11px;
    padding-bottom: 11px;
    padding-right: 24px;
    padding-left: 24px;
  }
}

.btn--made {
  @extend %btn-secondary;
  @include desktop {
    padding-right: 20px;
    padding-left: 20px;
  }
}

.buttons__list {
  display: flex;
}

.buttons__item {
  &:not(:last-child) {
    margin-right: 8px;
  }
  @include desktop {
    &:not(:last-child) {
      margin-right: 12px;
    }
  }
}

.achive {
  display: none;
  @include tablet {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-right: 8px;
  }
  @include tablet {
    padding-right: 15px;
  }
}

.achive__amount {
  position: relative;
  font-family: $fontSecondary;
  font-weight: 400;
  font-size: 28px;
  line-height: 1.14;
  text-align: right;

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: getColor('primary-text');
  z-index: 999;

  &::after {
    content: '';
    position: absolute;
    background-color: #fff;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    right: -4px;
    z-index: -1;
  }
  @include desktop {
    font-size: 46px;
    line-height: 1.15;

    &::after {
      content: '';
      position: absolute;
      background-color: #fff;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      right: -15px;
      z-index: -1;
    }
  }
}

.achive__position {
  font-family: $fontPrimery;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25;
  text-align: right;
  color: getColor('white-text');

  @include desktop {
    font-size: 14px;
    line-height: 1.28;
  }
}

.achive__title {
  &:not(:last-child) {
    margin-bottom: 16px;
  }
  @include desktop {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}

.hero__text--wrap {
  display: none;
  @include tablet {
    // position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 114px;
    // left: 184px;
    // bottom: 56px;
    margin-left: 184px;
  }
  @include desktop {
    max-width: 200px;
    left: 229px;
    bottom: 84px;
    margin-left: 213px;
  }
}

.hero__text {
  font-size: 8px;
  line-height: 150%;
  color: getColor('white-color');
  margin-bottom: 7px;

  @include desktop {
    font-size: 14px;
    line-height: 140%;
    margin-bottom: 20px;
  }
}

.btn--more {
  border: none;
  width: 18px;
  height: 18px;
  background-color: transparent;
  margin: 0;
  padding: 0;
  cursor: pointer;

  @include desktop {
    width: 26px;
    height: 26px;
  }
}

.icecream {
  position: absolute;
  bottom: 0px;
  left: 36px;

  @include tablet {
    left: 248px;
  }
  @include desktop {
    left: 517px;
  }
}

.girl {
  display: none;
  @include tablet {
    display: block;
    position: absolute;
    right: 32px;
    bottom: -56px;
  }
  @include desktop {
    right: 16px;
    bottom: -84px;
  }
}
.milk {
  display: none;
  @include tablet {
    display: block;
    position: absolute;
    left: 32px;
    bottom: -40px;
  }

  @include desktop {
    display: block;
    position: absolute;
    left: 16px;
    bottom: -40px;
  }
}
